import {isBlank} from '@/modules/utils/commonFunctions';

export class TokenSymbol {
    public static fromString(str: string): TokenSymbol {
        const split: string[] = str.split(',');
        if (split.length !== 2) {
            throw new Error('Wrong symbol format');
        }
        return new TokenSymbol(split[1], parseInt(split[0], 10));
    }

    public constructor(public readonly symbol: string,
                       public readonly precision: number) {
    }
}

export class Token {
    public static readonly EOS = new Token('EOS', 'eosio.token', 4);
    public static readonly ALL_TOKENS: Token[] = [Token.EOS];

    public static initialize() {
        for (const token of this.ALL_TOKENS) {
            this.TOKEN_CONTRACT_MAP.set(token.contract, token);
            this.TOKEN_SYMBOL_MAP.set(token.symbol, token);
        }
    }

    public static fromSymbol(symbol: string): Token {
        const result: Token | undefined = this.TOKEN_SYMBOL_MAP.get(symbol);
        if (isBlank(result)) {
            throw new Error('Token does not exist for symbol ' + symbol);
        }
        return result!;
    }

    public static fromContract(contract: string): Token {
        const result: Token | undefined = this.TOKEN_CONTRACT_MAP.get(contract);
        if (isBlank(result)) {
            throw new Error('Token does not exist for contract ' + contract);
        }
        return result!;
    }

    private static readonly TOKEN_CONTRACT_MAP: Map<string, Token> = new Map();
    private static readonly TOKEN_SYMBOL_MAP: Map<string, Token> = new Map();

    public readonly baseUnit: number;

    public constructor(public readonly symbol: string,
                       public readonly contract: string,
                       public readonly precision: number) {
        this.baseUnit = Math.pow(10, precision);
    }
}
Token.initialize();
