export class Dates {
    public static initialize() {
        if (new Date('1970-01-01T00:00:00').getTime() === 0) {
            this.ISO_SUFFIX = '';
        } else if (new Date('1970-01-01T00:00:00-0000').getTime() === 0) {
            this.ISO_SUFFIX = '-0000';
        }
    }

    public static toISOTimestamp(str: string): number {
        return new Date(str + this.ISO_SUFFIX).getTime() / 1000;
    }

    private static ISO_SUFFIX: string = '';
}
Dates.initialize();

