import {Contract} from '../../eos/contract';
import {EOS} from '../../eos/eos';
import {GlobalVarTable} from '@/modules/contract/dice/globalVarTable';
import {TokensTable} from '@/modules/contract/dice/tokensTable';
import {Asset} from '@/modules/eos/asset';
import {HistoryTable} from '@/modules/contract/dice/HistoryTable';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';

export enum RollMode {
    Under = 1,
    Over = 2,
}

export class Dice extends Contract {
    public readonly globalVarTable: GlobalVarTable;
    public readonly tokensTable: TokensTable;
    public readonly historyTable: HistoryTable;

    public constructor(contract: string, eos: EOS) {
        super(contract, eos);

        this.globalVarTable = new GlobalVarTable(eos, this.name);
        this.tokensTable = new TokensTable(eos, this.name);
        this.historyTable = new HistoryTable(eos, this.name);
    }

    public init(): Promise<any> {
        return this.pushAction('init', {});
    }

    public setActive(active: boolean): Promise<any> {
        return this.pushAction('setactive', {active});
    }

    public play(rollMode: RollMode, betNumber: number, asset: Asset): Promise<TransactionReceipt> {
        return this.transfer(
            asset, `${rollMode},${betNumber},${''},`,
        );
    }
}
