import {Token} from '@/modules/eos/token';
import {toFixed} from '@/modules/utils/commonFunctions';

export class Asset {
    public static fromRawAmount(amount: number, token: Token) {
        return new Asset(amount / token.baseUnit, token);
    }

    public static fromString(str: string): Asset {
        const split: string[] = str.split(' ');
        if (split.length !== 2) {
            throw new Error('Wrong asset format');
        }
        const token: Token = Token.fromSymbol(split[1]);
        return new Asset(parseFloat(split[0]), token);
    }

    constructor(public readonly quantity: number,
                public readonly token: Token) {
    }

    public toString(): string {
        return toFixed(this.quantity, this.token.precision) + ' ' + this.token.symbol;
    }

    public add(value: number): Asset {
        return new Asset(this.quantity + value, this.token);
    }

    public minus(value: number): Asset {
        return new Asset(this.quantity - value, this.token);
    }
}
