// @ts-ignore
import Eos from 'eosjs';
// @ts-ignore
import ScatterJS from 'scatterjs-core';
// @ts-ignore
import ScatterEOS from 'scatterjs-plugin-eosjs';
import {Network} from './network';
import {isPresent} from '../utils/commonFunctions';
import {Account} from '@/modules/eos/account';
import {EOSConnector} from '@/modules/eos/eosConnector';

export class Scatter implements EOSConnector {
    public eos: any;
    private scatter: any;

    public constructor(public readonly network: Network) {
        ScatterJS.plugins( new ScatterEOS() );
        const that: Scatter = this;

        ScatterJS.scatter.connect('Dice').then((connected: boolean) => {
            // User does not have Scatter Desktop, Mobile or Classic installed.
            if (!connected) {
                return false;
            }
            that.scatter = ScatterJS.scatter;
            that.eos = that.scatter.eos(network, Eos, {} );
            // @ts-ignore
            window.ScatterJS = null;
            return true;
        });
    }

    public async login(): Promise<any> {
        if (!this.initialized) {
            throw new Error('Scatter plugin has not been initialized');
        }
        return this.scatter.getIdentity({
            accounts: [this.network],
        });
    }

    public get initialized(): boolean {
            return isPresent(this.scatter);
    }

    public get loggedIn(): boolean {
        return this.initialized && isPresent(this.scatter.identity);
    }

    public async logout(): Promise<any> {
        if (this.loggedIn) {
            return this.scatter.logout();
        }
    }

    public getAccount(): Account {
        if (this.loggedIn) {
            for (const account of this.scatter.identity.accounts) {
                if (account.blockchain === 'eos') {
                    return new Account(account.name, account.authority);
                }
            }
        }
        throw new Error('No account found, make sure you have logged into a wallet');
    }
}

