import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';

export interface GlobalVarTableRow {
    id: number;
    val: number;
}

export class GlobalVarTable extends Table<GlobalVarTableRow> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'globalvars', 'id', contract);
    }

    protected convert(rows: any[]): GlobalVarTableRow[] {
        return rows as GlobalVarTableRow[];
    }
}
