import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';

export interface TokenRow {
    sym: string;
    contract: string;
    in: number;
    out: number;
    protect: number;
    times: number;
    divi_time: number;
    divi_balance: number;
    min_bet: number;
    large_bet: number;
}

export class TokensTable extends Table<TokenRow> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'tokens', 'sym', contract);
    }

    protected convert(row: any[]): TokenRow[] {
        return row as TokenRow[];
    }

}
