import {Table} from '@/modules/eos/table';
import {EOS} from '@/modules/eos/eos';
import {Asset} from '@/modules/eos/asset';
import {Token} from '@/modules/eos/token';
import {isEmptyArray} from '@/modules/utils/commonFunctions';
import {RollMode} from '@/modules/contract/dice/dice';
import {Dates} from '@/modules/utils/dates';

export class HistoryRow {
    public id: number;
    public betId: number;
    public player: string;
    public betAmount: Asset;

    public betNumber: number;
    public rollNumber: number;
    public payout: Asset;
    public profit: Asset;
    public rollMode: RollMode;

    public time: string;
    public timestamp: number;

    constructor(value: any) {
        const token: Token = Token.fromContract(value.contract);
        this.id = value.id;
        this.betId = value.bet_id;
        this.player = value.bettor;

        this.betNumber = value.roll_border;
        this.rollNumber = value.roll_value;
        this.rollMode = value.roll_type;
        this.betAmount = Asset.fromRawAmount(value.bet_amt, token);
        this.payout = isEmptyArray(value.payout) ?
                new Asset(0, Token.EOS) : Asset.fromString(value.payout[0]);
        this.time = value.time;
        this.timestamp = Dates.toISOTimestamp(this.time);

        this.profit = this.isWin ? this.payout.minus(this.betAmount.quantity) : new Asset(0, Token.EOS);
    }

    public get isRollOver(): boolean {
        return this.rollMode === RollMode.Over;
    }

    public get isWin(): boolean {
        return this.payout.quantity !== 0;
    }

}

export class HistoryTable extends Table<HistoryRow> {
    constructor(eos: EOS, contract: string) {
        super(eos, 'activebets', 'id', contract);
    }

    protected convert(row: any[]): HistoryRow[] {
        return row.map((value) => new HistoryRow(value)).sort((a, b) =>
                b.timestamp - a.timestamp,
        );
    }
}
