import {EOS} from '@/modules/eos/eos';
import {Asset} from '@/modules/eos/asset';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';

export class Contract {
    public constructor(public readonly name: string,
                       private readonly eos: EOS) {
    }

    public pushAction(name: string, data: any): Promise<TransactionReceipt> {
        return this.eos.call(this.name, name, data);
    }

    public transfer(asset: Asset, memo = ''): Promise<TransactionReceipt> {
        return this.eos.transfer(this.name, asset, memo);
    }
}
