import {Table} from '@/modules/eos/table';
import {Asset} from '@/modules/eos/asset';
import {EOS} from '@/modules/eos/eos';
import {Token} from '@/modules/eos/token';

export class AccountsTable extends Table<Asset> {
  constructor(eos: EOS, tokenContract: string, name: string) {
    super(eos, 'accounts', 'currency', tokenContract, name);
  }

  public async getCurrency(token: Token): Promise<Asset> {
      const result: any = await this.read(token.symbol, 1);
      if (result.length === 0) {
          return new Asset(0, token);
      } else {
          return Asset.fromString(result[0].balance);
      }
  }
}

export class EOSAccountsTable extends AccountsTable {
    constructor(eos: EOS, name: string) {
        super(eos, Token.EOS.contract, name);
    }

    public async getBalance(): Promise<Asset> {
        return this.getCurrency(Token.EOS);
    }
}
