import {EOS} from '@/modules/eos/eos';

export class Table<T> {
    public constructor(private readonly eos: EOS,
                       public readonly name: string,
                       public readonly key: string,
                       public readonly code: string,
                       public readonly scope: string = code) {
    }

    public async readAll(): Promise<T[]> {
        const keySet: Set<string> = new Set();
        const totalResult: any[] = [];
        let result: any = {};
        let lastKey: string = '';

        do {
            result = await this.eos.getTableRows(this, 500, lastKey);
            for (const row of result.rows) {
                lastKey = row[this.key];

                if (!keySet.has(lastKey)) {
                    keySet.add(lastKey);
                    totalResult.push(row);
                }
            }
        } while (result.more);

        return this.convert(totalResult);
    }

    public async read(lower: string, limit: number): Promise<T[]> {
        const result: any = await this.eos.getTableRows(this, limit, lower);
        return this.convert(result.rows);
    }

    public async getById(id: string): Promise<T> {
        const result: any = await this.eos.getTableRows(this, 1, id);
        return this.convert(result.rows)[0];
    }

    protected convert(row: any[]): T[] {
        return row as T[];
    }
}
