// @ts-ignore
import eosjs from 'eosjs';
import {isBoolean} from '@/modules/utils/commonFunctions';
import {EOSConnector} from '@/modules/eos/eosConnector';
import {Asset} from '@/modules/eos/asset';
import {Account} from '@/modules/eos/account';
import {Table} from '@/modules/eos/table';
import {TransactionReceipt} from '@/modules/eos/transactionReceipt';
import {Network} from '@/modules/eos/network';

export class EOS {
    private static processType(input: any): any {
        for (const key in input) {
            if (input.hasOwnProperty(key)) {
                const value: any = input[key];

                if (isBoolean(value)) {
                    input[key] = (value === true) ? 1 : 0;
                }
            }
        }
        return input;
    }
    private readonly eos: any;

    constructor(public readonly connector: EOSConnector) {
        const network: Network = connector.network;
        this.eos = eosjs({
            chainId: network.chainId,
            httpEndpoint: `${network.protocol}://${network.host}:${network.port}`,
            expireInSeconds: 60,
            broadcast: true,
            verbose: false, // API activity
            sign: true,
        });
    }

    public loggedIn(): boolean {
        return this.connector.loggedIn;
    }

    public connect(): Promise<any> {
        return this.connector.login();
    }

    public get account(): Account {
        return this.connector.getAccount();
    }

    public getTableRows(table: Table<any>, limit: number, lower: string): Promise<any> {
        return this.eos.getTableRows({
            scope: table.scope,
            code: table.code,
            table: table.name,
            json: true,
            limit,
            lower_bound: lower,
        });
    }

    public async call(contract: string, actionName: string, data: any): Promise<TransactionReceipt> {
        await this.ensureConnected();
        const account: Account = this.connector.getAccount();
        const result: any = await this.connector.eos.transaction({
            actions: [{
                account: contract,
                name: actionName,
                authorization: [{
                    actor: account!.name,
                    permission: account!.authority,
                }],
                data: EOS.processType(data),
            }],
        });
        return new TransactionReceipt(result);
    }

    public async transfer(to: string, asset: Asset, memo: string = ''): Promise<TransactionReceipt> {
        await this.ensureConnected();
        const account: Account = this.connector.getAccount();
        return this.call(asset.token.contract, 'transfer', {
            from: account.name,
            to,
            quantity: asset.toString(),
            memo,
        });
    }

    public getTransaction(id: string): Promise<any> {
        return this.eos.getTransaction(id);
    }

    private async ensureConnected(): Promise<any> {
        if (!this.loggedIn()) {
            await this.connector.login();
        }

        if (!this.loggedIn()) {
            throw new Error('Can not login to EOS network, user not logged in');
        }
    }
}
